import React, { Component } from 'react';
import '../../public/css/mymodal.css';


class MyModal extends Component{

    render(){
        var titulo;
        var texto;
        switch (this.props.wich) {
            case 1:
                texto=<p>Estes lembretes so notificaes que envimos sempre que algo estiver para comear. <br/>Ou quando tivermos uma sugesto para fazer.</p>;
                titulo= <h4>Lembretes</h4>;
                break;
            case 2:
                texto=<p>Com a opo de sugestes activada, o nosso sistema usar os seus dados para encontrar algo novo e a seu gosto. <br/><br/>Se tambm activar os lembretes, receber estas sugestes como notificaes.<br/><br/>A opo de zapping enviar sugestes extra para que facilmente alternar entre opes.<br/><br/>Defina as suas preferncias televisivas, para que lhe possamos proporcionar uma experincia de TV melhorada!</p>;
                titulo= <h4>Sugestes</h4>;
                break;
            case 3:
                texto=<p>Esta opo serve para saber se mais algum que voc conhea tambm esteja a ver o mesmo que voc.<br/>Escolha a fonte dos contactos mais conveniente (pode criar um gruo nos seus contatos e adicionar s esse grupo) e estes estaro visveis enquanto v televiso.</p>;
                titulo= <h4>Radar Social</h4>;
                break;
            default:
                titulo= "Erro. No h esclarecimento para dar. Feche esta janela";
        }

        if(this.props.modal==true){
            return(
                <div className="backg-modal" onClick={() => this.props.onClick()}>
                    <div className="modal-minha">
                        {titulo}
                        <br/>
                        {texto}
                        <br/>
                        <button className="btn btn-sm btn-primary" onClick={() => this.props.onClick()}>
                            close
                        </button>
                    </div>
                </div>);
        }else {
            return (
                <div id="nomodal"></div>

            );
        }
    }
}

export default MyModal;


