import React, { Component } from 'react';
import MyModal from './MyModal';
import question from '../../public/img/question_btn.png';
import { Link } from 'react-router';
import { Button } from 'react-bootstrap/lib/Button';
import '../../public/css/options.css';

import swal from 'sweetalert';
import '../../node_modules/sweetalert/dist/sweetalert.css';

class Options extends Component {

    constructor() {
        super();
        /*this.state = {
            openmodal: false,
            wichmodal: 0,
        };*///                                          sem a MyModal.js, isto no necessrio


        this.kickAmodal = this.kickAmodal.bind(this);//     nem sei se  preciso
    }

    handleClick(e){
        this.setState({openmodal: false});
    }

    kickAmodal(x){    //onClick={() => this.kickAmodal(3)}
        /*var check = this.state.openmodal;//           usar com modal feita  mo -> MyModal.js
         this.setState({openmodal: !check});//          a soluo a baixo  ligeiramentemais elegante
         this.setState({wichmodal: x});//               pode sernecessrio alterar o conteudo nas variaveis 'titulo'e'texto'para strings.
         */
        /*<MyModal modal={this.state.openmodal} wich={this.state.wichmodal} onClick={() => this.handleClick()}/>*/

        var titulo = "Erro desconhecido";
        var texto = "No h esclarecimento para dar. Feche esta janela.";
        switch (x) {
            case 1:
                texto="Erro 1";//<p>Estes lembretes so notificaes que envimos sempre que algo estiver para comear. <br/>Ou quando tivermos uma sugesto para fazer.</p>;
                titulo= "Erro 1";//<h4>Lembretes</h4>;
                break;
            case 2:
                texto="Erro 2" ;//<p>Com a opo de sugestes activada, o nosso sistema usar os seus dados para encontrar algo novo e a seu gosto. <br/><br/>Se tambm activar os lembretes, receber estas sugestes como notificaes.<br/><br/>A opo de zapping enviar sugestes extra para que facilmente alternar entre opes.<br/><br/>Defina as suas preferncias televisivas, para que lhe possamos proporcionar uma experincia de TV melhorada!</p>;
                titulo= "Erro 2";//<h4>Sugestes</h4>;
                break;
            case 3:
                texto="Erro 3";//<p>Esta opo serve para saber se mais algum que voc conhea tambm esteja a ver o mesmo que voc.<br/>Escolha a fonte dos contactos mais conveniente (pode criar um gruo nos seus contatos e adicionar s esse grupo) e estes estaro visveis enquanto v televiso.</p>;
                titulo="Erro 3";// <h4>Radar Social</h4>;
                break;
            default:
                titulo = "Erro desconhecido";
                texto = "No h esclarecimento para dar. Feche esta janela.";
        }
        swal(titulo,texto,"info");
    }

    render (){

        return(
            <div className="opcoesAlignCenter">
                <form className="form-horizontal" >
                    <fieldset> <legend>Alterar definies </legend>
                        <fieldset className="opcoesAlignLeft">
                            <legend>Lembretes:
                                <img className="question-mark" src={question} alt="What is this?"  />
                            </legend>
                            <p className="btpd">Quando quer receber lembretes?</p>
                            <Button bsStyle="primary">Primary</Button>
                            <div className="form-group pdr">
                                <div className="col-xs-10">
                                    <label><input type="radio" name="whenMemo" value="interval"/> Intervalo de horas definido: <br/>
                                        <div className="col-xs-offset-1 col-xs-10">
                                            Desde:
                                            <input type="time" className="form-control" id="inputPassword" placeholder="DEV_TIME_SELECTOR" />
                                            At:
                                            <input type="time" className="form-control" id="inputPassword" placeholder="DEV_TIME_SELECTOR" />
                                        </div>
                                    </label><br/>
                                    <label><input type="radio" name="whenMemo" value="agenda"/> Segundo disponibilidade na minha agenda.</label><br/>
                                    <label><input type="radio" name="whenMemo" value="neartv" /> Quando estou prximo da TV.</label><br/>

                                </div>
                            </div>
                        </fieldset>

                        <fieldset className="opcoesAlignLeft">
                            <legend>Sugestes:
                                <img className="question-mark" src={question} alt="What is this?"  />
                            </legend>
                            <div className="form-group pdr">
                                <div className="col-xs-10">
                                    <label><input type="checkbox" id="checkSuges" /> Activar sugestes de contdos novos</label><br/>
                                    <label><input type="checkbox" id="checkZapp" /> Activar opo de Zapping</label><br/>
                                    <button className="btn-default btn butino" id="mudarPrefs"><Link to="/prefs"> Mudar preferncias</Link></button>
                                </div>
                            </div>
                        </fieldset>

                        <fieldset className="opcoesAlignLeft">
                            <legend>Radar Social:
                                <img className="question-mark" src={question} alt="What is this?"  />
                            </legend>
                            <p className="btpd">Como quer associar os seus contactos??</p>
                            <div className="form-group pdr">
                                <div className="col-xs-10">
                                    <label><input type="radio" name="whatContacts" value="google" /> Contactos Google.</label><br/>
                                    <label><input type="radio" name="whatContacts" value="facebook"/> Contactos Facebook.</label><br/>
                                    <label><input type="radio" name="whatContacts" value="tele"/> Contactos Telemvel.</label><br/>
                                    <label><input type="radio" name="whatContacts" value="box"/> Pessoas associadas  mesma box.</label>
                                </div>
                            </div>
                        </fieldset>

                        <fieldset className="opcoesAlignLeft">
                            <div className="form-group">
                                <div className="col-xs-offset-2 col-xs-10 n-pdr">
                                    <button type="button" className="btn btn-primary col-xs-8">Guardar Alteraes</button>
                                </div>
                            </div>
                        </fieldset>
                    </fieldset>


                </form>
            </div>
        );
    }
}

export default Options;


